import React,{useState,useEffect} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import { Page } from 'react-pdf';
import { Document } from 'react-pdf/dist/entry.webpack';
import {toast} from 'react-toastify'
import {Button} from '@material-ui/core'
const useStyles = makeStyles(theme => ({
  appBar: {
    position: 'relative',
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function FullScreenDialog(props) {
  const classes = useStyles();
  const [pagenumber,setPageNumber]=useState(1)
  const [numpages,setNumberPages]=useState(null)
  useEffect(()=>{
    if(numpages){
      document.getElementsByClassName("canvas-docs")[0].oncontextmenu=function(){toast.error("Please do not tamper pdf");return false}
    }
  },[numpages])
  const handleClose = () => {
    props.setOpen(false);
  };
  const onDocumentLoadSuccess=({numPages})=>{
    setNumberPages(numPages)
  }
  const goToPrev=()=>{
    if(pagenumber!=1){
      setPageNumber(pagenumber-1)
    }else{
      toast.warn("You are at the bigining of the pdf file")
    }
  }
  const goToNext=()=>{
    if(pagenumber!=numpages){
      setPageNumber(pagenumber+1)
    }else{
      toast.warn("You have reached on the last page")
    }
  }
  return (
    <div>
      <Dialog fullScreen open={props.open} onClose={handleClose} TransitionComponent={Transition}>
        <AppBar className={classes.appBar}>
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              Document
            </Typography>
          </Toolbar>
        </AppBar>
        <br/>
        <div className="row" style={{justifyContent:"center",marginTop:20}}>
        <Button onClick={goToPrev} color="primary" variant="contained">Prev</Button>
        <Button onClick={goToNext}  color="secondary" variant="contained" style={{marginLeft:10}}>Next</Button>
        </div>
        <Document
        file={props.docs?props.docs:null}
        onLoadSuccess={onDocumentLoadSuccess}
        onLoadError={(err)=>{
          console.log(err.message)
        }}
        className="canvas-docs"
        >
          <Page pageNumber={pagenumber}/>
        </Document>
      </Dialog>
    </div>
  );
}
